import * as React from "react";
import * as ReactDOM from "react-dom";
import { DropDownList } from '@progress/kendo-react-dropdowns';

export interface IAdvancedSearchProps {
    selectedConditions?: Array<Condition>
}

export interface IAdvancedSearchState {
    hidden: boolean,
    attributes: Array<Attribute>,
    operators: Array<Operator>,
    selectedConditions: Array<Condition>,
    rows: Array<JSX.Element>,
}

export interface IAdvancedSearchRowProps {
    
    rowIndex?: number,
    attributes?: any,
    operators?: any,
}

export interface IAdvancedSearchRowState {
    attributeValue?: any,
    operatorValue?: any,
    operatorData?: any,
    attributeData?: any,

}

export class AdvancedSearchHeader extends React.Component {
    render() {
        return (<div className="row form-group">
            <div className="col-sm-4">
                <label htmlFor="conditionAttribute">Attribute</label>
            </div>
            <div className="col-sm-4">
                <label htmlFor="Operator">Operator</label>
            </div>
            <div className="col-sm-4">
                <label htmlFor="Value">Value</label>
            </div>
        </div>);

    }
}

export class AdvancedSearchAddNewRow extends React.Component {
    render() {
        return (<div className="row">
            <div className="col">
                <a href="#">Add Another Condition</a>
            </div>
        </div>);
    }

}

export class AdvancedSearch extends React.Component<IAdvancedSearchProps, IAdvancedSearchState> {
    constructor(props: IAdvancedSearchProps) {
        super(props);
        let arr = new Array<JSX.Element>();      
        
        this.state = {
            hidden: false,            
            operators: new Array<Operator>(),
            attributes: new Array<Attribute>(),
            rows: arr,
            selectedConditions:[],
        }
        ;
        this.addRow = this.addRow.bind(this);
    }

    render() {        
        
        return (!this.state.hidden && <div className="container">
            <AdvancedSearchHeader />
            {this.state.rows.map(row=>row)}            
            <div className="row">
                <div className="col">
                    <a href="#" onClick={this.addRow}>Add Another Condition</a>
                </div>
            </div>
            </div>
        );
    }

    componentDidMount() {
        var currentAttributeList = this.state.attributes;
        var currentOperatorList = this.state.operators;

        fetch("/ResourceAttribute/Get?resourceIds=Contract")
            .then(res => res.json())
            .then(
            (result) => {
                
                currentAttributeList = result.map((x: any) => {
                    return new Attribute(x.resourceAttributeGuid, x.name, x.title, x.attributeType);
                });
                

                this.setState({ attributes: currentAttributeList });
                this.addRow();     
            });

        fetch("/QueryOperator/Get")
            .then(res => res.json())
            .then((result) => {
                currentOperatorList = result.map((x: any) => {
                    return new Operator(x.queryOperatorGuid, x.name, x.title, x.type);
                });
                this.setState({ operators: currentOperatorList });
            });
        
        
        
    }

    addRow() {        
        let currentRows = this.state.rows;
        var count = currentRows.length + 1;
        currentRows.push(<AdvancedSearchRow key={count} attributes={this.state.attributes} operators={this.state.operators} />);
        this.setState({ rows: currentRows});
    }

    

}

export class AdvancedSearchRow extends React.Component<IAdvancedSearchRowProps, IAdvancedSearchRowState> {
    constructor(props: Readonly<IAdvancedSearchRowProps>) {
        super(props);
        this.onAttributeChange = this.onAttributeChange.bind(this);
        this.state =
         {
            attributeValue: { text: 'Select attribute...', id: '' },
            operatorValue: { text: 'Select operator...', id: '' },
            operatorData: [],
            attributeData:[],
         }
    }
    render() {  
        
        return(
        <div className="row form-group">
                <div className="col-sm-4">
                    <DropDownList data={this.state.attributeData} value={this.state.attributeValue} textField="text" dataItemKey="id" onChange={this.onAttributeChange} />
            </div>
                <div className="col-sm-4">
                    <DropDownList data={this.state.operatorData} value={this.state.operatorValue} textField="text" dataItemKey="id" />
                </div>
            <div className="col-sm-4">
            </div>
        </div>)
    }

    componentDidMount() {
        this.setState({ attributeData: this.getAttributeData() });

    }

    private getAttributeData(): any {

        var attributeData = this.props.attributes.map((x:any) => {
            let item: any = {};
            item["text"] = x.AttributeTitle;
            item["id"] = x.AttributeName;
            return item;
        });
        return attributeData;
    }

    private getOperatorData(operators:any): any {
        var attributeData = operators.map((x:any) => {
            let item: any = {};
            item["text"] = x.OperatorTitle;
            item["id"] = x.OperatorName;
            return item;
        });
        return attributeData;

    }

    onAttributeChange(event: any) {
       
        let selectedAttribute = this.props.attributes.filter((y: any) => {
            return y.AttributeName == event.target.value.id;
        });

        
        
        let operatorData = this.props.operators.filter((x: any) => {
            return x.OperatorType == selectedAttribute[0].AttributeType;
        });

        

        operatorData = this.getOperatorData(operatorData);
        this.setState({ attributeValue: event.target.value, operatorData: operatorData });
        
    }



    
}

class Condition {
    ConditionId: string;
    Attribute: Attribute;
    Operator: Operator;
    Value: any
}
class Resource {
    ResourceId: string;
    ResourceName: string;
    ResourceTitle: string;
}
class Attribute {
    AttributeId: string;
    AttributeName: string;
    AttributeTitle: string;
    AttributeType: number;

    constructor(attributeId: any, attributeName: string, attributeTitle: string, attributeType: number) {
        this.AttributeId = attributeId;
        this.AttributeName = attributeName;
        this.AttributeTitle = attributeTitle;
        this.AttributeType = attributeType;
    }
}
class Operator {
    OperatorId: string;
    OperatorName: number;
    OperatorTitle: string;
    OperatorType: number;

    constructor(operatorId: string, operatorName: number, operatorTitle: string, operatorType: number) {
        this.OperatorId = operatorId;
        this.OperatorName = operatorName;
        this.OperatorTitle = operatorTitle;
        this.OperatorType = operatorType;

    }
}