import * as React from "react"
import * as ReactDOM from "react-dom"
import { Dialog, DialogActionsBar } from "@progress/kendo-react-dialogs"
import { AdvancedSearch } from "./AdvancedSearch";
import { Button } from "@progress/kendo-react-buttons";

interface IAdvancedSearchDialogProps {

}

interface IAdvancedSearchDialogState {
    visible: boolean;
}

export class AdvancedSearchDialog extends React.Component<IAdvancedSearchDialogProps,IAdvancedSearchDialogState> {
    constructor(props:any) {
        super(props);
        this.state = {
            visible: false
        };
        this.toggleDialog = this.toggleDialog.bind(this);
    }

    toggleDialog()
    {
        var currentState = this.state.visible;
        this.setState({ visible: !currentState });
    }
    render()
    {       
        return (<div>
            {!this.state.visible && <a href="#" onClick={this.toggleDialog}>Click here to select</a>}

            {this.state.visible && <Dialog title="Advanced Search" width="80%" height="70%">
                <div className="container">
                    <AdvancedSearch />
                </div>

                <DialogActionsBar>
                    
                    <Button primary={true}>Apply</Button>
                    <Button onClick={this.toggleDialog}>Cancel</Button>
                </DialogActionsBar>
            </Dialog>}
            </div>
        );


       
    }



}